.nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: flex;
  flex-direction: column;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 95%;
  transform: translateX(-50%);
  top: 30%;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

.nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: #ffe700;
  font-size: 1.1rem;
}

.nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

.nav a.active {
  background: black;
  color: #ffe700;
}